
import * as React from "react"

function PinCircle({analog, digital, pwm, x, y, trigger, setting=0, radius=20}) {
    let color = "white";
    if(setting === 1)
    {
      color= "url('#pinGradientHigh')";
    }
    else if(setting === 2)
    {
      color= "url('#pinGradientHighIn')";
    }
    else if(setting === 3)
    {
      color= "url('#pinGradientPwmOn')";
    }
    else if(setting === 4)
    {
      color= "url('#pinDisabled')";
    }
  return(
      <>
        <circle style={{cursor:'pointer'}} class="clickable" id={digital} onClick={() => {trigger({Type: "PINS", Analog: analog, Digital: digital, Pwm: pwm})}} cx={x} cy={y} r={radius} fill={color}>
          <title>
            Digital: {digital.toString().substring(1) + "\n"}
            Analog: {analog === "NA" ? "Not Available \n" : analog + "\n"}
            Pwm: {pwm === "NA" ? "Not Available\n" : "Available\n"}
          </title>  
        </circle>
          
      </>
    )
}

export default PinCircle