import React, { useState, useEffect } from 'react';
import DataDisplay from './DataDisplay.js';
   
function Controller({reader, writer}) {
  let textBuffer = "";//Remove
   //Handle Pin data
  const [pinData, setPinData] = useState(null);
  const[deviceData, setDevices] =  useState({ pwm : {} });
 
   //Data Ping Interval
  const [intervalId, setIntervalId] = useState(null);
 
   //Data Ping Interval amount
   //TODO: Create dropdowns to set these intervals
  let intervalUpdate = 50;
 
  useEffect(() =>{//Ping board for pin data after setting a new port
    if(writer !== null)
    {
      queryBoard("PINS");
      startReader();
    }
  }, [writer]);
    
  /**
   * This method will accept a string message, encode it, and transmit it, via the Serial Writer, to the connected Serial Device.
   * This method ensures the writer is not null, to avoid a potential null exception.
   * 
   * @param {string} dataPackage 
   */
  function write(dataPackage)
  {
    const encoder = new TextEncoder();
    writer.write(encoder.encode(dataPackage)).catch(() =>{
      if(intervalId !== null)
      {
          clearInterval(intervalId);
          setIntervalId(null);
      }
      console.error("Can't send message. Writer set to null, ensure port has been set");
    });
  }/* End write(dataPackage) */

  /**
   * This method handles most of the Live Debugger's communication with the Serial Device. The selection parameter determines what type 
   * of information is being requested from or passed to the Serial Device. The data parameter is an optional object that holds any values needed
   * for the message being sent.
   * 
   * @param {string} selection 
   * @param {object} data 
   */
  function queryBoard(selection, data)
  {
    if(writer)
    {//Ensure writer has been set/created before trying to query the board
      try{
        //Don't call for new data until all existing data has been received
        while(textBuffer !== ""){}
        let id = null;
        let dataPackage = null;
        switch(selection)
        {
          case 'PINS'://Set a reocurring interval to ping for current pin states
            if(intervalId !== null)
            {
                clearInterval(intervalId);
            }
            id = setInterval(() =>
            {
              if(textBuffer == "")
              {
                write('0,');
              }
            }, intervalUpdate);
            setIntervalId(id);
            break;
            
          case 'SOUND'://Set a reoccuring interval to ping for current sound levels (decibels)
            if(intervalId !== null)
            {
                clearInterval(intervalId);
            }
            id = setInterval(() =>
            {
              if(textBuffer == "")
              {
                write('6,');
              }
            }, intervalUpdate);
            setIntervalId(id);
            break;

          case 'CAP'://Set a reoccuring interval to ping for current capacitive touch sensor states
            if(intervalId !== null)
            {
                clearInterval(intervalId);
            }
            id = setInterval(() =>
            {
              if(textBuffer == "")
              {
                write('5,');
              }
            }, intervalUpdate);
            setIntervalId(id);
            break;

            case 'MOTION'://Set a reoccuring interval to ping for current accelerometer (x,y,z) data
              if(intervalId !== null)
              {
                  clearInterval(intervalId);
              }
              id = setInterval(() =>
              {
                if(textBuffer == "")
                {
                  write('7,');
                }
              }, intervalUpdate);
              setIntervalId(id);
              break;

            case 'PWM': //Have microcontroller update a pin's PWM/AnalogWrite()
              dataPackage = "2," + data.pin.toString().substring(1) +"," +   data.value.toString() + "|";
              let copy = deviceData;
              copy.pwm[data.pin.toString()] = data.value;
              setDevices(copy);
              write(dataPackage);
              break;
          
            case 'PINMODE': //Update a pin's mode
              dataPackage = "3," + data.pin.toString().substring(1) +"," +   data.value.toString() + "|";
              write(dataPackage);
              break;
          
            case 'DIGITAL': //Update a pin's digital value
              dataPackage = "4," + data.pin.toString().substring(1) +"," +   data.value.toString() + "|";
              write(dataPackage);
              break;

            case 'TONE': //Have a speaker place a specific tone (frequency) for a set time interval
              if(intervalId !== null)
              {
                  clearInterval(intervalId);
              }
              dataPackage = "8," + data.freq.toString() +"," +   data.time.toString() + "|";
              write(dataPackage);
              break;

            case 'NEO': //Update a neopixel's color
              dataPackage = "9," + data.pixel.toString() +"," +   data.red.toString() +"," +   data.green.toString() +"," +   data.blue.toString()+ "|";
              write(dataPackage);
              break;
        }
      }
      catch(e){
        console.log(e);
      }
    }
  }/* End queryBoard(selection, data) */
  
  /**
   * This method will serve as a listener, constantly reading from the Serial Device and updating the Live Debugger's states based on values read in.
   * This is intended to be the only method that uses the Serial Reader. Any other location may endup consuming the Serial Buffer and cause the system to 
   * miss updates from the microcontroller
   */
  async function startReader(){
    const decoder = new TextDecoder();
    while (true) {  
        const { value, done } = await reader.read();
        if (value) {
          textBuffer = textBuffer + decoder.decode(value);
          if(textBuffer.includes('&'))
          {
            let index = textBuffer.indexOf('&');
            let data = textBuffer.substring(0, index);
            textBuffer = textBuffer.substring(index + 1);
            try{
              let jsonMsg = JSON.parse(data);
              if(jsonMsg.hasOwnProperty("Message"))
              { //Remove: Was intended for handling Serial Messages, but that may no longer be needed/supported.
                  //setText(prevText => prevText + jsonMsg.Message);
                  //scrollToBottom(document.getElementById('serial-textarea'));
              }
              if(jsonMsg.hasOwnProperty("Pins"))
              {
                  setPinData(jsonMsg.Pins);
              }
              if(jsonMsg.hasOwnProperty("Motion"))
              {
                  let motion = {motion: jsonMsg.Motion};
                  setDevices(deviceData => { return {...deviceData, ...motion }});
              }
              if(jsonMsg.hasOwnProperty("Sound"))
              {
                  let sound = {sound: jsonMsg.Sound};
                  setDevices(deviceData => { return {...deviceData, ...sound }});
              }
              textBuffer = "";//Reset textBuffer - assumes a complete message for each JSON string
            }
            catch(e)
            {
                console.log("Can't parse yet");
            }
          }
        }
        if (done) {
          console.log('[readLoop] DONE', done);
          reader.releaseLock();
          break;
        }
      }
  }/* End  startReader() */

  return(
    <>
     <DataDisplay pinData={pinData} deviceData={deviceData} serialCom={queryBoard} />
    </>
  )
}

export default Controller