
import * as React from "react"

function PinSelector({id, analog, digital, pwm, x, y, trigger, setting=0, width, height, rotation = 0, text_direction = "ltr", place_text_to_right = true, class_name=null}) {
  let identifier ="pinSelector";
  let font_size = (height) * 0.125 / 2;
  let title = "Digital: " + digital.toString().substring(1) + "\n Analog: "+ (analog === "NA" ? "Not Available" : analog) + "\nPwm: " + (pwm === "NA" ? "Not Available\n" : "Available\n");
  //Handle Pin Status Color
  let color = "white";
  if(setting === 1)
  {
    color= "url('#pinGradientHigh')";
  }
  else if(setting === 2)
  {
    color= "url('#pinGradientHighIn')";
  }
  else if(setting === 3)
  {
    color= "url('#pinGradientPwmOn')";
  }
  else if(setting === 4)
  {
    color= "url('#pinDisabled')";
  }
  else if(setting === 5)
  {
    color = "url('#cant_control')";
    identifier = "cantControl";
    title="";
  }

  //Handle text placement (left or right side)  
  let rect_x = x;
  let border_x = rect_x + width * 0.45;
  let text_x = border_x + 2.25;
  if(!place_text_to_right)
  {
    text_x = x;
    border_x = text_x + width * 0.515;
    rect_x = border_x + 2.25;
  }
  return(
      <>
      <g transform={"rotate(" + String(rotation) + "," + String(x + width/2) + "," + String(y + height/2)  + ")" }className={identifier} style={{cursor:'pointer', pointerEvents: "bounding-box"}} onClick={() => {trigger({Type: "PINS", Analog: analog, Digital: digital, Pwm: pwm, Class : class_name === null ? undefined : class_name})}}>
        <rect class="rect_hover" id={digital + "_rect"} x={text_x} y={y} fill="white" width={width * 0.52} height={height} /> {/* Box that changes color when the pin selector is being hovered over */}
        <text id={digital + "_text"}  x={text_x + (width * 0.52) * 0.25} y={y + height * 0.825 } fill="black" style={{direction:{text_direction}, fontSize:String(font_size) + "em", fontWeight:"bold"}}>{id}</text> {/* Pin's ID*/}
        <rect x={rect_x} y={y} fill={color} width={width * 0.45} height={height} /> {/* Box that is set to the color of the pin's current status */}
        <rect x={border_x} y={y} fill="black" width="2.5" height={height} /> {/* Edge border between color status & pin ID */}
        <rect stroke="black" strokeWidth="2.5" x={x} y={y} width={width} height={height} fill="transparent">  {/* Border around entire pin selector */}
          <title>
            {title}
          </title> 
        </rect>
      </g>
      </>
    )
}

export default PinSelector