import * as React from "react"
import PinCircle from "../../PinControls/PinCircle.js"
import PinSelector from "../../PinControls/PinSelector.js"
import Board from "./Board.js"
import PinKey from "../../PinControls/PinKey.js";


function Layout({pinData, trigger, disabled, deviceData}) {
  /***
   * 
   * 
   * This code block sets digitalValues -> an array that keeps track of a pin's current setting (Low: 0, High: 1, High-In: 2, PWM-ON: 3, Disabled: 4), 
   * this is passed to the  PinCircle component as a setting prop, which determines the pin's color.
   * This is all hardcoded for the the Circuit Playground Classic's pin/sensor ids.
   */
  //TODO Convert to use object/key pair and then use ids for the keys down below     <g transform="rotate(90, 102.029, 102.5125)">
      return (
          <>
            {/* Animations for the pin's current state (Low, High, High-In, PWM-ON, Disabled*/}
            <defs>  
                <linearGradient id="pinGradientHigh" x1="0%" y1="0%" x2="100%" y2="100%" > 
                    <stop offset="0%" stopColor="red">
                        <animate attributeName="stop-color" values="black;red;black" dur="1.5s" repeatCount="indefinite"></animate>
                    </stop>
                </linearGradient>
                <linearGradient id="pinGradientHighIn" x1="0%" y1="0%" x2="100%" y2="100%" > 
                    <stop offset="0%" stopColor="yellow">
                        <animate attributeName="stop-color" values="black;yellow;black" dur="1.5s" repeatCount="indefinite"></animate>
                    </stop>
                </linearGradient>
                <linearGradient id="pinGradientPwmOn" x1="0%" y1="0%" x2="100%" y2="100%" > 
                    <stop offset="0%" stopColor="#66CCEE">
                        <animate attributeName="stop-color" values="black;#66CCEE;black" dur="1.5s" repeatCount="indefinite"></animate>
                    </stop>
                </linearGradient>
                <linearGradient id="pinDisabled" x1="0%" y1="0%" x2="100%" y2="100%" > 
                    <stop offset="0%" stopColor="grey">
                        <animate attributeName="stop-color" values="grey" dur="1.5s" repeatCount="indefinite"></animate>
                    </stop>
                </linearGradient>
                <linearGradient id="cant_control" x1="0%" y1="0%" x2="100%" y2="100%" > 
                    <stop offset="0%" stopColor="#d4ae1b">
                        <animate attributeName="stop-color" values="#d4ae1b" dur="1.5s" repeatCount="indefinite"></animate>
                    </stop>
                </linearGradient>
               
              </defs>
            
            
            {/*Pin Holes */}
            <PinCircle analog="A0" digital="D0" pwm="D0" x={876} y={619.5} trigger={trigger} setting={0}/>
            <PinCircle analog="A1" digital="D1" pwm="D1" x={876} y={514} trigger={trigger} setting={0} />
            <PinCircle analog="A2" digital="D2" pwm="D2" x={876} y={393.5} trigger={trigger} setting={0}/>

            {/* Hardware Pins, Listed Top -> Bottom (following sideways orientation)*/}
            <PinSelector id="GND" analog="NA" digital="NA" pwm="NA" x={850} y={117.5} trigger={() => alert("Ground Pin")} setting={5} width={95} height={30} line_x={744} line_y={130} line_h={5} line_w={110} />
            
            {/* Group 1 */}
            <PinSelector id="P19" analog="NA" digital="D19" pwm="NA" x={1180} y={223} trigger={trigger} width={70} height={20} setting={0} line_x={744} line_y={239} line_h={5} line_w={435}/>
            <PinSelector id="P19" analog="NA" digital="D19" pwm="NA" x={1070} y={210} trigger={trigger} width={70} height={20} setting={0} line_x={744} line_y={226} line_h={5} line_w={335}/>
            <PinSelector id="P20" analog="NA" digital="D20" pwm="NA" x={960} y={197} trigger={trigger} width={70} height={20} setting={0} line_x={744} line_y={213} line_h={5} line_w={235}/>
            <PinSelector id="GND" analog="NA" digital="NA" pwm="NA" x={850} y={184} trigger={() => alert("Ground Pin")} width={70} height={20}  setting={5} line_x={744} line_y={200}  line_h={5} line_w={110}/>

            {/* Ensure the Pin Selector Lines are placed underneath the microcontroller */}
            <g transform="rotate(-90, 735, 370),translate(0,-175)">
              <Board width={575} height={600} x={420.08} y={125} />
            </g>


            {/* On Board Sensors*/}
            <PinKey x={0} y={115} /> 
         </>
    
    )
}

export default Layout